.background {
  background: linear-gradient(to right, #98fb98, #daa520);
}

.cat-info {
  flex-wrap: wrap;
  display: flex;
}

.cat-container {
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.cat-container img {
  max-width: 640px;
  max-height: 480px;
  width: auto;
  height: auto;
  transition: transform .3s;
  display: block;
}

.cat-container:hover img {
  transform: scale(1.05);
}

.cat-info-container {
  width: 300px;
  height: 100%;
  box-sizing: border-box;
  background-color: #fff;
  border: 2px solid #000;
  padding: 10px;
  position: absolute;
  top: 0;
  left: calc(100% + 20px);
}

.cat-info-container h2 {
  margin-top: 0;
}

.hidden {
  display: none;
}

.error {
  color: #e01111;
  font-size: 120%;
  animation: pulse 1s infinite alternate;
}

@keyframes pulse {
  0% {
    font-size: 120%;
  }

  100% {
    font-size: 180%;
  }
}

.cat-info img {
  max-width: 640px;
  max-height: 480px;
  width: auto;
  height: auto;
}

